.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
ul {
  list-style-type: none;
}
/*keeps each movie li element equally separated*/
.movie li {
    display:-ms-inline-flexbox;
    display:inline-flex;
    width: 25%;

}
/*keeps app from resizing*/
.App {
  width: 100%;
  height: 100%;
  min-width: 1000px;
  max-width: 1000px;
}
/*draws perimeter of outer list on the right hand side and left hand side*/
#list{
   border-right-style: solid;
   border-left-style: solid;

}
/*//creates top line of the entire list*/
.header {
  border-top-style: solid;
}
/*//creates perimeter lines on the left hand side and the bottom */
.movie{
  border-left-style: solid;
  border-bottom-style: solid;
}
/*makes sure each list element not a picture is lined up in the middle*/
.notPicture {
  vertical-align: 500%;
}
/*creates pointer icon when hovering over box office header*/
a{
  cursor: pointer;

}
/*sets width and height for image*/
img {
   height: auto; 
    width: auto; 
    max-width: 200px; 
    max-height: 200px;
}
/*creates arrow*/
i {
    border: solid black;
    border-width: 0 3px 3px 0;
    display: inline-block;
    padding: 3px;
}
/*up arrow*/
.up {
    -ms-transform: rotate(-135deg);
        transform: rotate(-135deg);
    -webkit-transform: rotate(-135deg);
}
/*down arrow*/
.down {
    -ms-transform: rotate(45deg);
        transform: rotate(45deg);
    -webkit-transform: rotate(45deg);
}
/*arrow inclosed in p tag*/
p{
  display: inline;
  margin-left: 10px;
}


